import { getToken, setToken, removeToken } from '@/utils/auth';
import firebase from "firebase/app";
import "firebase/auth";

const state = {
  token: getToken(),
  name: '',
  roles: [],
};

const mutations = {
  SET_TOKEN: (state, token) => {
    state.token = token;
  },

  SET_NAME: (state, name) => {
    state.name = name;
  },

  SET_ROLES: (state, roles) => {
    state.roles = roles;
  },
};

const actions = {
  // user login
  login({commit,dispatch}, data) {
    const { email, password } = data;
    return new Promise((resolve, reject) => {
      firebase.auth().signInWithEmailAndPassword(email, password)
        .then((userCredential) => {
        var user = userCredential.user;
        commit('SET_TOKEN', user.refreshToken);
        commit('SET_NAME', user.email);
        setToken(user.refreshToken);
        resolve();
      },
      (err) => {
        reject(err)
    });
  });
  },
  SET_USER({commit},data){
    commit('SET_NAME',data.email)
  },
  SET_TOKEN({commit},data){
    commit('SET_TOKEN',data)
    setToken(data);
  },

  // user signup
  signup({ commit }, userInfo) {
    const { name, email, password } = userInfo;
    return new Promise((resolve, reject) => {
      firebase.auth().signInWithEmailAndPassword(data.email, data.password).then(
        (user) => {
          return user
        },
        (err) => {
          return err
        }
      );
    });
  },


  // get user info
  getUser({ commit, dispatch,state }) {
    return new Promise((resolve, reject) => {
      const token = getToken();
      firebase.auth().signInWithCustomToken(token)
        .then((userCredential) => {
          // Signed in
          var user = userCredential.user;
          commit('SET_NAME', user.email);
          resolve();

          // ...
        })
        .catch((error) => {
          // var errorCode = error.code;
          // var errorMessage = error.message;
          return error;
          // ...
        });
    });
  },

  // user logout
  logout({ commit, state }) {
    firebase.auth().signOut().then(() => {
          // Sign-out successful.
          commit('SET_NAME', '');
          commit('SET_TOKEN', '');
          removeToken();
        }).catch((error) => {
          console.log(error)
          // An error happened.
        });

  },
  // user logout

  resetToken({ commit }) {
    return new Promise(resolve => {
      commit('SET_TOKEN', '');
      commit('SET_ROLES', []);
      removeToken();
      resolve();
    });
  },

  // Dynamically modify permissions
  // changeRoles({ commit, dispatch }, role) {
  // },
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
};
