import Vue from 'vue'
import Router from 'vue-router'
import Layout from '@/views/frontend/Layout.vue'
import Home from '@/views/frontend/Home.vue'

Vue.use(Router)

export default new Router({
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      component: Layout,
      meta:{
        title:'Home'
      },
      children:[
        {
          path:'',
          component:Home,
          name: 'home',

        },
        {
          path:'about',
          component: () => import(/* webpackChunkName: "about" */ './views/frontend/About.vue'),
          name: 'about',
        },


        {
          path: '/blogs',
          name: 'blogs',
          component: () => import(/* webpackChunkName: "about" */ './views/frontend/blogs/Blogs.vue')
        },
        {
          path: '/blog/:id',
          name: 'blog',
          component: () => import(/* webpackChunkName: "about" */ './views/frontend/blogs/Blog.vue')
        },

      ]
    },

    {
      path: '/backend',
      meta:{
        role:'admin'
      },
      component: () => import(/* webpackChunkName: "about" */ './views/backend/Layout.vue'),
      children:[{
        path:'',
        component: () => import(/* webpackChunkName: "about" */ './views/backend/Index.vue'),
        name: 'backend',
        meta:{
          role:'admin'
        },
      }]
    },
    {
      path: '/login',
      name: 'login',
      component: () => import(/* webpackChunkName: "about" */ './views/auth/Login.vue')
    },

    {
   path: '*',
   component: Layout,
children:[
  {
    path:'',
    name: 'catchAll',
    component:Home
  }
]
}
  ]
})
